body{ overflow: hidden;}

.App{
width: 100%;
display: -ms-flexbox;
display: flex;
-ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
background:#000;
}

#appSidebar{
  width: 18.75rem;
  height: calc(100vh );
  background:#000;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  -webkit-transition: all .4s ease-out;
  -o-transition: all .4s ease-out;
  transition: all .4s ease-out; 
}

#appSidebar.hidden{ 
  -webkit-transform: translate3d(-18.75rem,0,0); 
          transform: translate3d(-18.75rem,0,0);
  margin-left:-18.75rem
}

#searchInput{ 
  width: calc(100% - 15px);
  margin-bottom: 5px; 
  margin-left:5px;
  padding-top: 5px;
  padding-bottom: 5px;
  -webkit-transition: width .5s ease-in;
  -o-transition: width .5s ease-in;
  transition: width .5s ease-in;
}


@media  screen  and (max-device-width: 600px) {

  #appSidebar{
    width: 12.75rem;
    height: calc(100vh );
    background:#000;
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
    -webkit-transition: all .4s ease-out;
    -o-transition: all .4s ease-out;
    transition: all .4s ease-out; 
  }
  
  #appSidebar.hidden{ 
    -webkit-transform: translate3d(-12.75rem,0,0); 
            transform: translate3d(-12.75rem,0,0);
    margin-left:-12.75rem
  }
  
  #searchInput{ 
    width: calc(100% - 15px);
    margin-bottom: 5px; 
    margin-left:5px;
    padding-top: 5px;
    padding-bottom: 5px;
    -webkit-transition: width .5s ease-in;
    -o-transition: width .5s ease-in;
    transition: width .5s ease-in;
  }


}

.mapContainer{
-ms-flex: 1 1 auto;
    flex: 1 1 auto;
height: calc( 100vh - 3.125rem );
}

.map{
  width: 100%; 
  height:100%;
}

.tophead{
  height: 3.125rem; 
  background-color: #000
}

.tophead button{
  border: none;
  font-weight: 800;
  color:#fff;
  background:#000;
  padding-left: 10px;
  padding-top: 10px;
  cursor: pointer;
  font-size: 20px;
 }


.locations-grid{ color:#fff; height: 18.75rem; overflow-y: scroll}
.locations-grid li{border-top: 1px solid #ccc; color:#fff; padding: 1.25rem; border-bottom: 1px solid #ccc  }
.locations-grid li a{color:#fff; text-decoration: none;  }


/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}



.locationPanel{
  background:#000;
  width: 250px;
  -webkit-transition: all 1s ease-out;
  -o-transition: all 1s ease-out;
  transition: all 1s ease-out; 
  color:#fff
  
}


.locationPanel img {
 max-width: 100%;
 margin-bottom: 20px; 
}

.locationPanel p {
  padding: 10px;
 }

 .locationPanel h3 {
  padding: 10px;
 }

 .locationPanel button {
  border: none;
  font-weight: 800;
  color:#fff;
  background:#000;
  padding-left: 10px;
  padding-top: 10px;
  cursor: pointer;
  font-size: 20px;
 }

.locationPanel.hidden{ 
 position: absolute;
 right:-250px;
 -webkit-transform: translate3d(400px,0,0);
         transform: translate3d(400px,0,0);
 width: 250px;
}


.loaderror{
  font-size:20px;
  padding: 50px;
  text-align: center;
  color:#fff
}


